<template>
  <div>
    <div class="list">
      <label class="list-item">
        <i>服务区域:</i>
      </label>
      <user-address ref="address"/>
      <el-button @click="addAddress">添加</el-button>
      <el-checkbox class="pl" v-model="allCity">全国</el-checkbox>
    </div>
    <div class="list">
      <span v-for="(address, index) in addressName" v-bind:key="index" class="pl">{{address}}</span>
    </div>
  </div>
</template>

<script>
import UserAddress from "components/address";
export default {
  name: "serveAddress",
  props: {
    addressName: {
      default: function() {return []},
      type: Array
    }
  },
  data() {
    return {
      addressList: [],
      allCity: false
    };
  },
  components: {
      UserAddress,
  },
  methods: {
    addAddress(val) {
      this.allCity = false;
      this.addressList = this.addressList.concat(this.$refs.address.getData().checkCity);
      this.addressName.push(this.$refs.address.getData().checkCity[0].areaName)
      console.log(this.addressName);
    },
    getData() {
      let addressCode = ''
      this.addressList.reduce((address) => {
        addressCode += address.areaCode + ','
      })
      return addressCode || '-1'
    },
    allCityChange() {
        if (this.allCity) this.addressList = []
    }
  },
  watch: {
      allCity(val) {
          console.log(val)
          if(val) {
            this.addressName = []
            this.addressList = []
          }
      }
  }
};
</script>

<style lang="less" scoped>
@import url('../assets/less/input.less');
.contianer {
  padding-left: 10px;
  text-align: left;
}
.title {
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
  padding-bottom: 5px;
}
.pl {
  padding-left: 36px;
}
.btn-submit{
  margin-top: 40px;
}
.width.el-input{
  width: 70px;
}
</style>
